import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import { Button, CardMedia, CardContent, CardActions, CardActionArea, Card, Typography, Paper } from '@material-ui/core';
import WeatherCard from '../components/card/index.js'
import { listWeatherAPI } from '../app/api/app_api';

const styles = {
  card: {
    maxWidth: 345,
  },
  media: {
    objectFit: 'cover',
  },
};

class App extends Component {
  constructor(props) {
    super(props)

    this.state = {
      weatherResponse: [],
      city: 'Jakarta',
      error: '',
      icon: '',
      initialLoading: true,
    }
  }

  async componentDidMount() {
    this.getWeather()
  }

  async getWeather() {
    try {
      const listWeathers = await listWeatherAPI(this.state.city)
      if (listWeathers.status !== 200) throw Error()
      this.setState({
        weatherResponse: listWeathers.data
      })
    } catch (e) {
      this.setState({ error: 'Terjadi kesalahan pada saat memuat data' })
    }
    this.setState({ initialLoading: false })
  }

  render() {
    const { city, weatherResponse } = this.state
    console.log(weatherResponse)
    return (
      <paper>
        {weatherResponse.map(n =>
          <WeatherCard
            city={city}
            image={n.location.name}
          />)
        }
      </paper>
    )
  }
}

App.propTypes = {
  classes: PropTypes.object.isRequired,
};

const mapStateToProps = (state) => ({
  city: state.city,
})

export default connect(mapStateToProps)(App)
